import React from 'react';
import { RouteHandler } from 'react-router';
import NavigationBar from '../navigation/NavigationBar';

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      searchText: null
    };

    this.handleNavChange = this.handleNavChange.bind(this);
    this.handleContextChange = this.handleContextChange.bind(this);
  }

  handleNavChange(options = {}) {
    this.setState({searchText: options.searchText});
  }

  handleContextChange(options = {}) {
    this.setState({title: options.title});
  }

  render() {
    return (
      <div className="app">
        <NavigationBar {...this.state} handleChange={this.handleNavChange} />
        <RouteHandler {...this.state} handleChange={this.handleContextChange} />
      </div>
    );
  }
}

App.contextTypes = {
  router: React.PropTypes.func
};



