import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
const {Spring} = require('react-motion');
import classNames from 'classnames';

import StoryListItem from './StoryListItem';
import { requireAuth } from '../../decorators/requireAuth';


const Status = {
  Public: 0,
  Private: 1,
  Open: 2
};


const StoryList = class StoryList extends ParseComponent {
  constructor(props) {
    super(props);
  }

  observe(props) {
    let storyQuery, matchQuery, matchAllQuery;

    if (props.searchText == null) {
      storyQuery = this._getBaseQuery();
    } else if (props.searchText === '') {
      storyQuery = this._getBaseQuery().doesNotExist('title');
    } else {
      let searchText = props.searchText.toLowerCase();
      let words = [''];
      words = searchText.split(' ').filter((w) => w !== '');

      matchQuery = this._getBaseQuery().matches('title', new RegExp(searchText, 'i'));
      matchAllQuery = this._getBaseQuery().containedIn('words', words);
      storyQuery = Parse.Query.or(matchAllQuery, matchQuery);
    }

    return {
      stories: storyQuery,
      bookmarks: new Parse.Query('Bookmark').equalTo('creator', Parse.User.current()).equalTo('typeRaw', 0).include('story'),
      user: ParseReact.currentUser
    };
  }

  _getBaseQuery() {
    return new Parse.Query('Story').equalTo('statusRaw', Status.Public).include('creator').descending('createdAt').notEqualTo('deleted', true);
  }

  renderBookmarks() {
    return (
      this.data.bookmarks.map((b) => {
        let s = b.story;
        return (
          <StoryListItem {...s}
                          key={s.id.objectId}
                          isBookmarked />
        );
      })
    );
  }

  renderPublic() {
    return (
      this.data.stories.map((s) => {
        let bookmarkedStories = this.data.bookmarks.filter((b) => {
          return b.typeRaw == 0 && b.story.id.objectId === s.id.objectId;
        });

        return (
          <StoryListItem {...s}
                          key={s.id.objectId}
                          isBookmarked={bookmarkedStories.length > 0} />
        );
      })
    );
  }

  render() {
    let storyList, stories;

    let bookmarkedPaths = this.context.router.getCurrentRoutes().filter((route) => {
      return route.name == 'bookmarkedStories';
    });
    let searchPaths = this.context.router.getCurrentRoutes().filter((route) => {
      return route.name == 'searchStories';
    });

    if (this.pendingQueries().length) {
      storyList = <div className='loading grid-frame'>Loading</div>;
    } else {
      if (bookmarkedPaths.length) {
        storyList = this.renderBookmarks();
      } else {
        storyList = this.renderPublic();
      }
    }

    let classList = classNames({
      'story-list': true,
      'bookmarks': bookmarkedPaths.length > 0,
      'search-results': searchPaths.length > 0,
      'grid-block': true,
      'small-up-2': true,
      'medium-up-3': true,
      'large-up-4': true,
      'xlarge-up-5': true,
      'xxlarge-up-6': true
    });

    return (
      <div className={classList}>
        {storyList}
      </div>
    );
  }
};

export default requireAuth(StoryList);
export let undecorated = StoryList;

StoryList.contextTypes = {
  router: React.PropTypes.func.isRequired
};



