import ga from 'react-ga';
import mixpanel from 'mixpanel';
import merge from 'webpack-merge';

import { Parse } from 'parse';
import ParseAuth from './ParseAuth';
import secrets from '../../config/secrets';

const Type = {
  User: 'User',
  Story: 'Story',
  Page: 'Page'
};

const analytics = {
  init() {
    if (ParseAuth.loggedIn()) {
      ga.initialize(secrets.ga, {'userId': Parse.User.current().id});
    } else {
      ga.initialize(secrets.ga);
    }

    mixpanel.init(secrets.mixpanel, {
      debug: false,
      loaded() {
        console.log('Analytics started');
      }
    });

  },

  identify(userId, traits = {}) {
    let email = traits.email;
    let first_name = traits.first_name;
    let last_name = traits.last_name;
    let user_type = traits.user_type;

    mixpanel.identify(userId);
    mixpanel.people.set({
      $email: email,
      user_type: user_type
    });
    mixpanel.people.set_once({
      $first_name: first_name,
      $last_name: last_name,
      $created: (new Date()).toISOString(),
    });
  },

  login() {
    mixpanel.register_once({
      'First Login Date': (new Date()).toISOString(),
    });
    mixpanel.people.increment('login_count');
    mixpanel.time_event('Logout');
  },

  page(category, properties = {}) {
    if (properties.path) {
      ga.pageview(properties.path);
      mixpanel.track(category, properties);
      mixpanel.people.increment('page_views');
    }
  },

  track(category, action, label, options = {}) {
    let dataObj = {
      category: category,
      action: action,
      label: label,
      value: options.value
    };

    ga.event(dataObj);

    dataObj = merge(dataObj, options);
    mixpanel.track(action, dataObj);

    if (action === 'Clio') {
      mixpanel.people.increment('clio_count');
    }
  }
};

export default analytics;



