import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import LabelListRow from '../labels/LabelListRow';
import NotificationSystem from 'react-notification-system';

const ParseComponent = ParseReact.Component(React);

export default class ManageLabels extends ParseComponent {
  constructor(props){
    super(props);

    this.state = {
      category: 'classState',
      submitting: false,
      _notificationSystem: null,
      isEditings: []
    };

    this.handleEditSave = this.handleEditSave.bind(this);

  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
  }

  observe(props) {
    const classQuery = new Parse.Query('Label').equalTo('typeRaw', 0).ascending('name');
    const assignmentQuery = new Parse.Query('Label').equalTo('typeRaw', 2).ascending('name');
    const genreQuery = new Parse.Query('Label').equalTo('typeRaw', 1).ascending('name');
    const miscQuery = new Parse.Query('Label').equalTo('typeRaw', 3).ascending('name');

    return {
      class: classQuery,
      genre: genreQuery,
      assignment: assignmentQuery,
      misc: miscQuery
    };
  }

  handleCreateNewLabelOnClick(e) {
    e.preventDefault();

    let newLabelValue = React.findDOMNode(this.refs.newLabel).value;

    if (this.state.submitting || newLabelValue == '') {
      this._notificationSystem.addNotification({
        message: 'Nothing to create',
        level: 'error'
      });
      return;
    }

    var newLabel = newLabelValue.trim();
    if (!newLabel) return;

    var typeRaw;

    if (this.state.category === 'classState') {
      if (this.checkForDuplicateInArray(this.data.class, newLabel)) {
        return;
      }
      typeRaw = 0;

    } else if (this.state.category === 'genreState') {
      if (this.checkForDuplicateInArray(this.data.genre, newLabel)) {
        return;
      }
      typeRaw = 1;

    } else if (this.state.category === 'assignmentState') {
      if (this.checkForDuplicateInArray(this.data.assignment, newLabel)) {
        return;
      }
      typeRaw = 2;

    } else if (this.state.category === 'miscState') {
      if (this.checkForDuplicateInArray(this.data.misc, newLabel)) {
        return;
      }
      typeRaw = 3;
    }

    this.setState({submitting:true});

    this._saveToParse(newLabelValue, typeRaw);
  }

  _saveToParse(newLabelValue, typeRaw) {
    ParseReact.Mutation.Create('Label', {
      name: newLabelValue,
      storyCount: 0,
      typeRaw: typeRaw
    }).dispatch().then(() => {
      React.findDOMNode(this.refs.newLabel).value = '';
      this.refreshQueries();
      this.setState({submitting:false});
      this._notificationSystem.addNotification({
        message: 'Label created',
        level: 'success'
      });
    });
  }

  handleEditSave(index, input) {
    if (input.editedLabel === '') {
       this._notificationSystem.addNotification({
        message: 'Please enter a label to update',
        level: 'info'
      });
      return;
    }

    if (this.state.submitting) {
       this._notificationSystem.addNotification({
        message: 'Submitting, please wait',
        level: 'info'
      });
      return;
    }

    if (this.state.category === 'classState') {
      if (this.checkForDuplicateInArray(this.data.class, input.editedLabel)) {
        return;
      }

    } else if (this.state.category === 'genreState') {
      if (this.checkForDuplicateInArray(this.data.genre, input.editedLabel)) {
        return;
      }

    } else if (this.state.category === 'assignmentState') {
      if (this.checkForDuplicateInArray(this.data.assignment, input.editedLabel)) {
        return;
      }

    } else if (this.state.category === 'miscState') {
      if (this.checkForDuplicateInArray(this.data.misc, input.editedLabel)) {
        return;
      }
    }

    this.setState({
      submitting: true
    });

    ParseReact.Mutation.Set(input.listItem, {
      name: input.editedLabel
    }).dispatch().then(() => {
      this.setState({
        submitting: false,
        isEditings: this.state.isEditings.filter((n) => n !== index)
      });
      this._notificationSystem.addNotification({
        message: 'Label updated',
        level: 'success'
      });
    });
  }

  checkForDuplicateInArray(array, newLabel) {
    for (var item of array) {
      if (item.name.toLowerCase() === newLabel.toLowerCase()) {
        this._notificationSystem.addNotification({
          message: 'Already have this label',
          level: 'error'
        });
        return true;
      }
    }
    return false;
  }

  handleCancelNewLabelOnClick() {
    React.findDOMNode(this.refs.newLabel).value = '';
  }

  handleClassOnClick() {
    if (this.state.category === 'classState') {return;}
    this.setState({
      category: 'classState',
      isEditings: []
    });
    React.findDOMNode(this.refs.newLabel).value = '';
  }

  handleGenreOnClick() {
    if (this.state.category === 'genreState') {return;}
    this.setState({
      category: 'genreState',
      isEditings: []
    });
    React.findDOMNode(this.refs.newLabel).value = '';
  }

  handleAssignmentOnClick() {
    if (this.state.category === 'assignmentState') {return;}
    this.setState({
      category: 'assignmentState',
      isEditings: []
    });
    React.findDOMNode(this.refs.newLabel).value = '';
  }

  handleMiscOnClick() {
    if (this.state.category === 'miscState') {return;}
    this.setState({
      category: 'miscState',
      isEditings: []
    });
    React.findDOMNode(this.refs.newLabel).value = '';
  }

  handleEdit(index) {
    this.setState({
      isEditings: this.state.isEditings.concat(index)
    });
  }

  handleEditCancel(index) {
    this.setState({isEditings: this.state.isEditings.filter((n) => n !== index)});
  }

  renderNewLabelRow() {
    return (
      <div className='grid-block'>
        <div className='grid-block new-label-row'>
          <div className='grid-block inputBox'>
            <input className='input-box' ref='newLabel' placeholder='New Label Name' />
          </div>
          <div className='grid-block shrink button hollow large-offset-3 cancelButton' onClick={this.handleCancelNewLabelOnClick.bind(this)}>Clear</div>
          <div className='grid-block shrink button success createButton' onClick={this.handleCreateNewLabelOnClick.bind(this)}>Create</div>
        </div>
      </div>
    );
  }

  renderLabelListRow(listItem, index) {
    return (
      <LabelListRow listItem={listItem}
        editState={this.state.isEditings.indexOf(index) !== -1}
        key={listItem.id.objectId}
        handleSave={this.handleEditSave.bind(this, index)}
        handleEditClick={this.handleEdit.bind(this, index)}
        handleEditCancelClick={this.handleEditCancel.bind(this, index)}/>
    );
  }

  render() {
    let list, listCount, newLabelRow;

    // generate list according to category state
    if (this.state.category === 'classState') {
      list = this.data.class.map((listItem, index) => {
        return this.renderLabelListRow(listItem, index);
      });
      listCount = this.data.class.length;
    } else if (this.state.category === 'genreState') {
      list = this.data.genre.map((listItem, index) => {
        return this.renderLabelListRow(listItem, index);
      });
      listCount = this.data.genre.length;
    } else if (this.state.category === 'assignmentState') {
      list = this.data.assignment.map((listItem, index) => {
        return this.renderLabelListRow(listItem, index);
      });
      listCount = this.data.assignment.length;
    } else if (this.state.category === 'miscState') {
      list = this.data.misc.map((listItem, index) => {
        return this.renderLabelListRow(listItem, index);
      });
      listCount = this.data.misc.length;
    }

    return (
      <section className='manage-labels grid-block vertical'>
        <div className='grid-container'>
          <div className='grid-block'>
            <div className='grid-block'>
              <div className='grid-content button hollow round categories' onClick={this.handleClassOnClick.bind(this)}>Class</div>
              <div className='grid-content button hollow categories' onClick={this.handleGenreOnClick.bind(this)}>Genre</div>
              <div className='grid-content button hollow categories' onClick={this.handleAssignmentOnClick.bind(this)}>Assignment</div>
              <div className='grid-content button hollow categories' onClick={this.handleMiscOnClick.bind(this)}>Misc</div>
            </div>
          </div>
          {this.renderNewLabelRow()}
          <div className='grid-block vertical list-block'>
            <div className='grid-content list-count'> {listCount} items</div>
            {list}
          </div>
        </div>
        <div>
          <NotificationSystem ref="notificationSystem" />
        </div>
      </section>
    );
  }
};





