import React from 'react';
import S from 'string';
import $ from 'jquery';
import rangy from 'rangy';
import 'rangy/lib/rangy-textrange';
import MediumEditor from 'medium-editor';

import Hint from '../pages/Hint';
import Hintify from '../../support/hintify';
import format from '../../support/HtmlFormatter';

function onEditableInput (e, editable) {
  let sel = rangy.getSelection();
  if (sel.anchorNode && sel.isCollapsed) {
    const parent = sel.getRangeAt(0).startContainer.parentElement;

    if ($(parent).is('span.aid')) {
      this.medium.saveSelection();
      $(parent).replaceWith(parent.textContent);
      this.medium.restoreSelection();
    };
  };
  let range = rangy.createRange();
  range.selectNodeContents(editable);
  const text = range.text();
  range.collapse(false);
  this.setState({text});
  this.change(text);
}
export default class Editor extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      text: this.props.text,
      proofreadedText: null,
      showedHint: false,
      initialized: false,
    };

    this.showHint = this.showHint.bind(this);
  }

  componentDidMount() {
    const dom = React.findDOMNode(this);
    this.medium = new MediumEditor(dom, this.props.options);
    this.medium.selectElement(React.findDOMNode(this));
    this.medium.subscribe('editableInput', onEditableInput.bind(this));
  }

  componentWillUnmount() {
    this.medium.destroy();
    this.hint && this.hint.destroy();
  }

  componentWillUpdate(nextProps, nextState) {
    this.medium && this.medium.saveSelection();
  }

  componentDidUpdate(prevProps, prevState) {
    this.medium.restoreSelection();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.text && !this.state.text && !this.state.initialized) {
      const innerHTML = format(nextProps.text);

      this.medium.setContent(innerHTML, 0);
      this.setState({text: nextProps.text, initialized: true});
    };

    if (nextProps.aids !== this.props.aids) {
      this.setState({showedHint: false});
      this.hint = new Hintify(React.findDOMNode(this), this.state.text, nextProps.aids.tags, true);
    };

    if (nextProps.showHint && !this.state.showedHint) {
      this.showHint();
      this.setState({showedHint: true});
    };
  }

  showHint() {
    if (this.hint) {
      this.medium.saveSelection();
      this.hint.destroy();
      this.medium.restoreSelection();
    };

    this.medium.saveSelection();
    React.findDOMNode(this).innerHTML = this.hint.htmlContent;
    this.medium.restoreSelection();
    console.log('editor show hint', this.hint);
    this.hint.showHint();
  }

  change(text) {
    if (this.props.onChange) {
      this.props.onChange(text);
    };
  }

  clear() {
    React.findDOMNode(this).innerHTML = '<p><br/></p>';
  }

  render() {
    return (
      <div
        className={this.props.className}
        contentEditable
        dangerouslySetHTML={{__html: (this.hint && this.hint.htmlContent) || this.state.text}}
      ></div>
    );
  }
}



