import React from 'react';

export default class LabelListRow extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      editState: props.editState
    };
  }

  handleEditButtonClick() {
    this.props.handleEditClick();
  }

  handleCancelButtonClick() {
    this.props.handleEditCancelClick();
  }

  handleSaveButtonClick() {
    this.props.handleSave({
      editedLabel: React.findDOMNode(this.refs.editedLabel).value,
      listItem: (this.props.listItem)
    });
  }

  render() {

    let listRow;

    if (!this.props.editState) {
      listRow = (
        <div className='grid-block list-group-item'>
          <div className='grid-block item-name'> {this.props.listItem.name} </div>
          <div className='grid-block story-count'> {this.props.listItem.storyCount + ' stories'}</div>
          <div className='grid-block shrink button small editButton' onClick={this.handleEditButtonClick.bind(this)}>Edit</div>
        </div>
      );
    } else {
      listRow = (
        <div className='grid-block list-group-item'>
          <div className='grid-block edit-block'>
            <input autoFocus className='grid-block shrink edit-box' ref='editedLabel' placeholder={this.props.listItem.name}/>
          </div>
          <div className='grid-block story-count'> {this.props.listItem.storyCount + ' stories'}</div>
          <div className='grid-block shrink button small editButton cancel-button' onClick={this.handleCancelButtonClick.bind(this)}>Cancel</div>
          <div className='grid-block shrink button small editButton save-button' onClick={this.handleSaveButtonClick.bind(this)}>Save</div>
        </div>
      );
    }

    return (
      <div className='grid-block list-group-item'>
        {listRow}
      </div>
    );
  }
};

LabelListRow.defaultProps = {
  editState: false,
  listItem: {name: 'Label Name', storyCount: 0}
};




