                    <path d='M14.5049504,26.1220144 C17.3921419,26.1220144 19.7326733,25.2238252 19.7326733,22.3366337 C19.7326733,19.4494421 17.3921421,17.1089109 14.5049505,17.1089109 C11.6177589,17.1089109 9.27722772,19.4494421 9.27722772,22.3366337 C9.27722772,25.2238252 11.6177588,26.1220144 14.5049504,26.1220144 Z' id='leftEye' opacity='0' fill='#D1ABFC'></path>
