export default function format (content) {
  if (content && content.length) {
    return content.split('\n').reduce((sum, text) => {
    if (text.length) {
      return `${sum}<p>${text}</p>`;
    } else {
      return `${sum}<p><br /></p>`;
    };
  }, '');
  } else {
    return content;
  };
}



