import React from 'react';
import Parse from 'parse';
// import ga from 'react-google-analytics';


export default class SignUp extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      emailSent: false,
      message: this.props.message,
      style: this.props.style
    };
    this.onSubmit = this.onSubmit.bind(this);
    this.emailIsValid = this.emailIsValid.bind(this);
    this.contact = new Parse.Object('Contact');
  }

  emailIsValid(email){
    var re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
    return re.test(email);
  }

  onSubmit(e) {

    // ga('create', 'UA-68630239-1', 'auto');
    // ga('send', 'event', 'AppStore', 'download', 'Landing Page');

    e.preventDefault();

    var email = React.findDOMNode(this.refs.emailInput).value;
    if ((email != '') && this.emailIsValid(email)) {
      this.contact.set('email', email);
      this.contact.save();
      this.setState({emailSent: true});
      console.log('Contact info sent');
    } else {
      console.log('empty or invalid email');
    }
  }

  render() {
    let form;
    if (!this.state.emailSent) {
      form = (
        <form className='signup-form'>
          <span className='inline-label'>
            <input ref='emailInput' type='email' placeholder='your@email.com' />
            <input className='button' type='submit' value='Sign Up' onClick={this.onSubmit} />
          </span>
        </form>
      );
    } else {
      form = (
        <div className='vertical grid-block'>
          <div className='grid-content'>
            <p className='get-email-text'>
              Thanks for getting in touch. We will get back to you asap.
            </p>
          </div>
        </div>
      );
    }

    return (form);
  }

}


