import React from 'react';
import Router from 'react-router';

export default class NotFound extends React.Component {
  constructor(props) {
    super(props);
    this.clioClick = this.clioClick.bind(this);
  }

  clioClick() {
    this.context.router.transitionTo('stories');
  }

  clio() {
    return (<svg width='150px' height='150px' viewBox='0 0 48 48' ref='clio-full'>
        <title>clio</title>
        <desc>Clio</desc>
        <defs></defs>
        <g id='Page-1' stroke='none' ref='clio-icon' strokeWidth='1' fill='none' fill-rule='evenodd'>
            <g id='Read---Submit' transform='translate(-1243.000000, -247.000000)'>
                <g id='clio' transform='translate(1243.000000, 247.000000)'>
                    <circle id='whiteBack' fill='#A60000' cx='24' cy='24' r='24'></circle>
                    <path d='M24,34.4310943 C36.7298901,34.4310943 47.049505,38.205329 47.049505,24.950495 C47.049505,11.6956611 36.7298901,0.95049505 24,0.95049505 C11.2701099,0.95049505 0.95049505,11.6956611 0.95049505,24.950495 C0.95049505,38.205329 11.2701099,34.4310943 24,34.4310943 Z' id='blackScreen' fill='#1E1E1E'></path>
                    <path d='M33.5049504,26.1220144 C36.3921419,26.1220144 38.7326733,25.2238252 38.7326733,22.3366337 C38.7326733,19.4494421 36.3921421,17.1089109 33.5049505,17.1089109 C30.6177589,17.1089109 28.2772277,19.4494421 28.2772277,22.3366337 C28.2772277,25.2238252 30.6177588,26.1220144 33.5049504,26.1220144 Z' id='rightEye' opacity='1' fill='#D1ABFC'></path>
                    <path d='M14.5049504,26.1220144 C17.3921419,26.1220144 19.7326733,25.2238252 19.7326733,22.3366337 C19.7326733,19.4494421 17.3921421,17.1089109 14.5049505,17.1089109 C11.6177589,17.1089109 9.27722772,19.4494421 9.27722772,22.3366337 C9.27722772,25.2238252 11.6177588,26.1220144 14.5049504,26.1220144 Z' id='leftEye' opacity='1' fill='#D1ABFC'></path>
                </g>
            </g>
        </g>
    </svg>);
  }

  render() {
    return(
      <div className='grid-container not-found'>
        <div className='grid-block vertical'>
          <div className='grid-block align-center'>
            <div onClick={this.clioClick.bind(this)}>
              {this.clio()}
            </div>
          </div>
          <div className='grid-block align-center'>
            <p className='not-found-text'>
              Clio isnt sure where you want to go.
              Click on her and she'll take you back to your stories.
            </p>
          </div>
        </div>
      </div>
    );
  }
}

NotFound.contextTypes = {
  router: React.PropTypes.func
};



