import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import classNames from 'classnames';
import NotificationSystem from 'react-notification-system';

var askUserAboutDeleting = false;

export default class DeleteStoryButton extends ParseComponent {
  constructor(props) {
    super(props);
    this.state = {
      saving: false
    };
    this.handleDelete = this.handleDelete.bind(this);
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
  }

  observe(props) {
    return {
      user: ParseReact.currentUser,
      stories: new Parse.Query('Story').equalTo('objectId', props.storyId)
    };
  }

  handleDelete(e) {
    if (askUserAboutDeleting === false) {
      askUserAboutDeleting = true;
      this._notificationSystem.addNotification({
        message: 'Are you sure you want to delete this story? If so, click Delete Story again',
        level: 'error',
        position: 'tc'
      });
    } else {
      askUserAboutDeleting = false;
      this.setState({saving: true});

      ParseReact.Mutation.Set(this.data.stories[0], {
        deleted: true
      }).dispatch().then(() => {
        this.props.onDelete();
      }, (error) => {
        console.log('delete story error', error);
      });
    }
  }

  render() {
    let creator, hide;
    hide = true;
    if(this.data.stories && this.data.stories.length && this.data.user) {
      creator = this.data.stories[0].creator;
      hide = this.data.user.objectId != creator.objectId;
    }

    if(this.data.user && this.data.user.typeRaw == '1') {
      hide = false;
    }

    return(
      <div className={classNames({hide})}>
        <div className="grid-block right">
          <div className='delete-story button hollow' onClick={this.handleDelete.bind(this)}>
            <span className={classNames({hide: this.state.saving})}>Delete Story</span>
            <div className={classNames('button-spinner', {hide: !this.state.saving})}></div>
          </div>
        </div>
        <div>
          <NotificationSystem ref="notificationSystem" />
        </div>
      </div>
    );
  }

}

DeleteStoryButton.propTypes = {
  storyId: React.PropTypes.string.isRequired
};



